export default {
  name: "processMonitor1",
  data() {
    return {
      flowSee: true,
      flowUnsee: false,
      stepItems: [],
      commentItems: [],
      data: [],
      active: 0,
      timer: null,
      currentActName: "",
      ifshowCurrentActName: false
    };
  },
  props: ["model"],
  created() {
   // console.info("processMonitor--------created---------");

    
  },
  activated() {
   // console.info("processMonitor--------activated---------");
    //console.log(this.model.Id)
    this.model.Id = this.$route.params.Id;
    this.loadData();
    //$("#steps").html("");
    //console.info("----"+$('#steps').html());
    
    
  },

  methods: {
     
    loadData() {
      var _this = this;
      
      _this.data = [];
      if (!this.util.isNotEmpty(this.model.Id)) {
        this.model.Id = this.util.getUrlParam("Id");
       // console.log("minitor-----------",this.model.Id)
      }

            _this.util.unmask();
      //this.util.post('/workflow/getHistoricTask', { procDefId: this.model.procDefId, procInstId: this.model.procInstId }, function (res) {
       // console.log(this.model.Id)
      this.util.post("/workflow/workflowAction!getComments.action", {businessKey:_this.model.Id},function(res) {
          if (res != undefined) {
            _this.data = res;
           // console.log(_this.data);
          //  console.info("------------------------");
          }
        }
      );

    },
    updateSteps() {
      var _this = this;
      var children = $("#steps").children();

      //console.info("------updateSteps-------"+children.length);
      if (children.length > 0) {
        //console.info("------可以了-------");
        var divs = $("#steps .el-step__icon");
        for (var i = 0; i < divs.length; i++) {
          var divlist = $(divs[i]).children();
          //console.info(divlist);
          if (divlist[0].tagName == "DIV") {
            $(divlist[0]).html(i + 1);
          }
        }
        //console.info(divs);
        clearInterval(_this.timer);
        _this.timer = null;
      } else {
        //console.info("_this.timer-----"+_this.timer);
        if (_this.timer == null) {
          _this.timer = setInterval(this.updateSteps, 100);
        } else {
          //this.updateSteps();
        }
      }
      //console.info($('#steps'));
    },
    flowShow() {
      this.flowSee = false;
      this.flowUnsee = true;
    },
    flowHide() {
      this.flowSee = true;
      this.flowUnsee = false;
    }, getComment(comment) {
      if (comment) {
        //if(comment.indexof('span')>-1){
        return comment.replace(/<.*?>/ig, "");
        //}
      }
      return comment;
    }, getContent(content) {
      if (content) {
        return content;
      }
      return '';
    }
  }
};



